.container-cart {
  transform: scaleY(0);
  transition: transform 0.4s;
  transform-origin: top;
  position: absolute;
  top: 129px;
  right: 11.5%;
  z-index: 100;
  padding: 31px 33px;
  width: 377px;
  height: auto;
  background-color: $color-white;
  border-radius: 8px;

  h3 {
    text-align: center;
  }

  .header-cart {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 25px;
    margin-bottom: 32px;

    p {
      font-weight: 700px;
    }

    .remove {
      color: rgba($color: #000000, $alpha: 0.5);
      cursor: pointer;

      &:hover {
        text-decoration: underline;
        color: $color-orange;
      }
    }
  }

  .div-total {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 25px;
    margin-bottom: 24px;
  }

  .item-div {
    width: 100%;
    height: auto;
    margin-bottom: 32px;

    .item:not(:last-child) {
      margin-bottom: 24px;
    }

    .item {
      display: flex;
      align-items: center;
      width: 100%;
      height: 64px;

      img {
        width: 64px;
        height: 64px;
        border-radius: 8px;
        margin-right: 16px;
      }
    }

    section {
      flex-direction: column;
      margin-right: auto;
      height: auto;

      p {
        font-weight: 700;
      }

      p:first-child {
        color: #000000;
      }

      p:last-child {
        color: rgba($color: #000000, $alpha: 0.5);
      }
    }

    .div-quantity {
      display: flex;
      width: 96px;
      height: 32px;
      background-color: $color-grey;

      p {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
      }

      button {
        width: 32px;
        height: 32px;
        color: rgba($color: #000000, $alpha: 0.25);
        background-color: none;
        cursor: pointer;
      }
    }
  }

  .checkout {
    width: 100%;
    height: 48px;
    background-color: $color-orange;
    color: $color-white;
    cursor: pointer;

    &:hover {
      background-color: $color-orange-hover;
    }
  }
}

@media screen and (max-width: 1460px) {
  .container-cart {
    right: 6%;
  }
}

@media screen and (max-width: 1180px) {
  .container-cart {
    right: 5%;
  }
}

@media screen and (max-width: 500px) {
  .container-cart {
    padding: 20px;
    width: 90%;
  }
}

.toggle-show {
  transform: scaleY(1);
}
