.container-order {
  position: absolute;
  top: 156px;
  left: 50%;
  transform: translateX(-50%);
  width: 540px;
  height: auto;
  background-color: $color-white;
  padding: 48px;
  z-index: 20;
  border-radius: 8px;

  .confirmation-image {
    margin-bottom: 33px;
  }
  .confirmation {
    margin-bottom: 32px;
  }

  h3 {
    margin-bottom: 24px;
  }

  .div-recap {
    display: flex;
    width: 100%;
    min-height: 140px;
    height: fit-content;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 46px;
  }

  .left-part {
    width: 247px;
    height: fit-content;
    background-color: $color-grey;
    padding: 24px;

    .div-items {
      overflow: hidden;
      width: 198px;
      height: 50px;
      margin-bottom: 12px;

      div:not(:last-child) {
        margin-bottom: 16px;
      }

      div {
        display: flex;
        height: 52px;

        img {
          align-self: center;
          margin-right: 16px;
          width: 50px;
          height: 50px;
        }

        section {
          margin-right: auto;
          width: auto;

          p:first-child {
            font-weight: 700;
          }
        }
      }
    }

    span {
      display: block;
      border-top: 1px solid rgba($color: #000000, $alpha: 0.08);
      padding-top: 12px;
      width: 100%;
      height: 30px;
      text-align: center;
      cursor: pointer;
    }
  }

  .right-part {
    display: flex;
    padding: 0 0 32px 32px;
    width: 198px;
    min-height: 140px;
    height: auto;
    background-color: #000;
    color: $color-white;

    section {
      align-self: flex-end;

      p:first-child {
        opacity: 0.8;
        margin-bottom: 8px;
      }
    }
  }

  button {
    background-color: $color-orange;
    color: $color-white;
    width: 100%;
    height: 48px;
    cursor: pointer;

    &:hover {
      background-color: $color-orange-hover;
    }
  }
}

@media screen and (max-width: 635px) {
  .container-order {
    width: 90%;

    .div-recap {
      flex-direction: column;
    }

    .left-part,
    .right-part {
      width: 100%;
    }

    .left-part {
      div {
        margin: auto;
      }
    }

    .right-part {
      padding: 0;
      section {
        align-self: center;
        margin: auto;
      }
    }
  }
}

@media screen and (max-width: 490px) {
  .container-order {
    .confirmation-image {
      display: block;
      margin: 0 auto 33px;
    }

    h3,
    .confirmation {
      text-align: center;
    }

    .div-recap {
      display: flex;
      width: 100%;
      min-height: 140px;
      height: fit-content;
      border-radius: 8px;
      overflow: hidden;
      margin-bottom: 46px;
    }
  }
}

@media screen and (max-width: 420px) {
  .container-order {
    padding: 32px;
  }
}

@media screen and (max-width: 370px) {
  .container-order {
    .left-part {
      .div-items {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 340px) {
  .container-order {
    .left-part {
      padding: 15px;
    }
  }
}
