.container-category {
  display: flex;
  gap: 5%;
  width: 1110px;
  height: 204px;
  margin: 0 auto 198px;

  section {
    padding-top: 116px;
    text-align: center;
    position: relative;
    width: 350px;
    height: 204px;
    background-color: $color-grey;
    border-radius: 8px;

    .category-title {
      text-transform: uppercase;
      font-weight: 700;
      margin-bottom: 10px;
    }

    .category-link {
      text-transform: uppercase;
      align-items: center;
      display: inline-flex;
      color: rgba($color: #000000, $alpha: 0.8);
      height: min-content;

      &:hover {
        color: $color-orange;
      }
    }

    .arrow {
      margin-left: 13px;
      height: 10px;
      width: 5px;
    }

    .image-category {
      width: 170px;
      position: absolute;
      top: -26%;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}

@media screen and (max-width: 1180px) {
  .container-category {
    width: 90%;
    margin: 0 auto 98px;
    section {
      height: 164px;
      padding-top: 88px;
    }
  }
}

@media screen and (max-width: 620px) {
  .container-category {
    align-items: center;
    flex-direction: column;
    height: auto;
    row-gap: 68px;
    margin: 0 auto 120px;

    section {
      padding-top: 88px;
      width: 100%;
    }
  }
}
