.container-checkout {
  width: 100%;
  margin: auto;
  min-height: 100vh;
  height: auto;
  background-color: $color-grey;

  .background {
    display: auto;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 96px;
    background-color: rgba($color: #000000, $alpha: 0.8);
  }

  .error-cart {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 500px;
    height: auto;
    text-align: center;

    h4 {
      text-transform: uppercase;
      margin-bottom: 20px;
    }

    h6 {
      padding: 10px 20px;
      margin: auto;
      width: max-content;
      color: $color-white;
      background-color: $color-orange;

      &:hover {
        background-color: $color-orange-hover;
      }
    }
  }

  .div-checkout {
    margin: 88px auto 145px;
    width: 1110px;
    height: auto;
  }

  .goBack {
    display: inline-flex;
    color: rgba($color: #000000, $alpha: 0.5);
    cursor: pointer;
    margin-bottom: 38px;
  }

  .form-summary {
    width: auto;
    height: auto;
    display: flex;
    justify-content: space-between;
  }

  form {
    padding: 54px 48px 48px;
    width: 730px;
    height: auto;
    background-color: $color-white;
    border-radius: 8px;

    h3 {
      margin-bottom: 38px;
    }

    .div-info:nth-child(2) {
      margin-bottom: 53px;
    }

    .div-info:nth-child(3) {
      margin-bottom: 61px;
    }

    label {
      font-weight: 700;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: -0.214286px;
      color: #000000;
    }

    ::placeholder {
      font-weight: 700;
      font-size: 14px;
      line-height: 19px;
      letter-spacing: -0.25px;
      color: rgba($color: #000000, $alpha: 0.4);
    }

    .selected {
      border: 1px solid $color-orange;
      accent-color: $color-orange;
    }

    .unselected {
      border: 1px solid#cfcfcf;
    }

    .error {
      position: absolute;
      right: 0;
      top: 0;
      width: auto;
      color: #cd2c2c;
    }

    .div-info {
      width: 100%;
      height: auto;

      h6 {
        color: $color-orange;
        margin-bottom: 16px;
      }
    }

    .info {
      display: flex;
      flex-flow: row wrap;
      column-gap: 14px;
      row-gap: 24px;
      width: 100%;
      height: auto;

      .section,
      section {
        position: relative;
        width: auto;

        &__address {
          width: 100%;
        }
      }

      label {
        display: block;
        margin-bottom: -4px;
      }
    }

    .container-radio {
      display: flex;
      justify-content: space-between;
      margin-bottom: 24px;

      .container-radio-button {
        height: auto;
        width: fit-content;

        section:first-child {
          margin-bottom: 16px;
        }

        section {
          display: flex;
          align-items: center;
          width: 309px;
          height: 56px;
          border-radius: 8px;
          border: 1px solid #cfcfcf;

          &:hover {
            border: 1px solid $color-orange-hover;
          }
        }

        input[type="radio"] + label {
          display: flex;
          align-items: center;
          height: 56px;
          width: 100%;
        }

        label,
        input[type="radio"] {
          margin-left: 16px;
          cursor: pointer;
        }
      }
    }

    .div-emoney {
      width: 100%;
      height: auto;
    }

    .div-cash {
      display: flex;
      align-items: center;
      height: auto;

      img {
        width: 48px;
        height: 48px;
        margin-right: 32px;
      }

      p {
        width: 554px;
        height: auto;
      }
    }
  }

  .input {
    border: 1px solid #cfcfcf;
    height: 56px;
    padding: 18px 24px;
    border-radius: 8px;

    &__small {
      width: 309px;
    }

    &__large {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1180px) {
  .container-checkout {
    .div-checkout {
      width: 90%;
    }

    .form-summary {
      flex-direction: column;
      row-gap: 32px;
      width: min-content;
      margin: auto;
    }
  }
}

@media screen and (max-width: 830px) {
  .container-checkout {
    .form-summary {
      width: 100%;
    }

    form {
      width: 100%;
    }
  }
}

@media screen and (max-width: 790px) {
  .container-checkout {
    .background {
      top: 90px;
    }
  }
}

@media screen and (max-width: 690px) {
  .container-checkout {
    form {
      .container-radio {
        flex-direction: column;

        .container-radio-button {
          margin: 17px 0 0 auto;
        }
      }
    }
  }
}

@media screen and (max-width: 530px) {
  .container-checkout {
    .error-cart {
      width: 90%;
    }
  }
}

@media screen and (max-width: 525px) {
  .container-checkout {
    .div-info {
      .section {
        width: 100% !important;
      }
    }

    form {
      padding: 23px;
    }

    .input {
      width: 100%;

      &__small {
        min-width: 100%;
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 490px) {
  .container-checkout {
    form {
      .div-cash {
        flex-direction: column;

        img {
          margin-bottom: 20px;
        }

        p {
          width: 100%;
          text-align: center;
        }
      }
    }
  }
}

@media screen and (max-width: 460px) {
  .container-checkout {
    form {
      .container-radio {
        .container-radio-button {
          width: 100%;
          section {
            width: 100%;
          }
        }
      }
    }
  }
}
