#root {
  width: 1440px;
  margin: auto;
  position: relative;
}

* {
  font-family: "Manrope", sans-serif;
  font-style: normal;
}

h1 {
  font-weight: 700;
  font-size: 56px;
  line-height: 58px;
  letter-spacing: 2px;
}

h2 {
  font-weight: 700;
  font-size: 40px;
  line-height: 44px;
  letter-spacing: 1.42857px;
}

h3 {
  font-weight: 700;
  font-size: 32px;
  line-height: 36px;
}

h4 {
  font-weight: 700;
  font-size: 28px;
  line-height: 38px;
  letter-spacing: 2px;
}

h5 {
  font-weight: 700;
  font-size: 24px;
  line-height: 33px;
  letter-spacing: 1.71429px;
}

h6 {
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
}

p {
  font-weight: 500;
  font-size: 15px;
  line-height: 25px;
}

.special {
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
  letter-spacing: 10px;
}

.see-more {
  font-weight: 700;
  font-size: 13px;
  line-height: 18px;
  letter-spacing: 1px;
}

.link {
  font-weight: 700;
  font-size: 13px;
  line-height: 25px;
  letter-spacing: 2px;
}

$color-white: #fff;
$color-orange: #d87d4a;
$color-black: #191919;
$color-grey: #f1f1f1;
$color-orange-hover: #fbaf85;
$color-error: #cd2c2c;

@media screen and (max-width: 1460px) {
  #root {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  h1 {
    font-weight: 700;
    font-size: 36px;
    line-height: 40px;
  }
}

@media screen and (max-width: 400px) {
  h2 {
    font-weight: 700;
    font-size: 28px;
    line-height: 38px;
    letter-spacing: 2px;
  }
}
