.container-article {
  width: 1110px;
  height: auto;
  margin: auto;
  margin-bottom: 240px;

  .goback {
    display: inline-flex;
    margin-bottom: 56px;
    cursor: pointer;
  }

  .div-article:not(:last-child) {
    margin-bottom: 160px;
  }

  .div-article:nth-child(2n) {
    flex-direction: row-reverse;
  }

  .reverse:nth-child(2n) {
    flex-direction: row;
  }

  .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    width: 160px;
    height: 48px;
    background-color: $color-orange;
    color: $color-white;

    &:hover {
      background-color: $color-orange-hover;
    }
  }

  .div-article {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: auto;

    .article-image {
      width: 540px;
      height: 560px;
      border-radius: 8px;
    }

    section {
      width: 445px;
      height: auto;

      .special {
        display: block;
        margin-bottom: 16px;
        color: $color-orange;
      }

      h2 {
        margin-bottom: 32px;
      }

      p {
        margin-bottom: 40px;
      }

      .price {
        display: block;
        margin-bottom: 47px;
        font-weight: 700;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .container-article {
    width: 90%;
    margin-bottom: 160px;

    .div-article {
      .article-image {
        width: 445px;
        height: 400px;
      }

      section {
        width: 400px;
      }
    }
  }
}

@media screen and (max-width: 980px) {
  .container-article {
    .div-article {
      .article-image {
        width: 400px;
        height: 400px;
      }
    }
  }
}

@media screen and (max-width: 930px) {
  .container-article {
    .btn {
      margin: auto;
    }

    .goback {
      margin-bottom: 24px;
    }

    .div-article:not(:last-child) {
      margin-bottom: 120px;
    }

    .div-article:nth-child(2n) {
      flex-direction: column;
    }

    .div-article {
      flex-direction: column;

      .article-image {
        width: 450px;
        height: auto;
        margin-bottom: 52px;
      }

      section {
        width: 460px;
        text-align: center;

        h2 {
          margin-bottom: 32px;
        }

        p {
          margin-bottom: 24px;
        }
      }
    }
  }
}

@media screen and (max-width: 510px) {
  .container-article {
    .div-article {
      .article-image {
        width: 100%;
      }

      section {
        width: 100%;
      }
    }
  }
}
