.container-home {
  width: 100%;

  .div-produit {
    width: 1110px;
    height: auto;
    margin: 0 auto 200px;

    .see-more {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 160px;
      height: 48px;

      &__full {
        background-color: #000;
        color: white;

        &:hover {
          background-color: $color-black;
        }
      }

      &__empty {
        border: 1px solid #000;
        color: #000;

        &:hover {
          background-color: #000;
          color: $color-white;
        }
      }
    }

    .redirection {
      display: flex;
      align-items: center;
      border-radius: 8px;
    }

    .redirection:nth-child(2),
    .redirection:nth-child(3) {
      height: 320px;
    }

    .redirection:not(:last-child) {
      margin-bottom: 48px;
    }

    .redirection:nth-child(1) {
      position: relative;
      height: 560px;
      background-color: $color-orange;
      overflow: hidden;

      img {
        position: absolute;
        bottom: -15px;
        left: 10%;
        width: 410px;
        height: 493px;
      }

      section {
        margin: 0 95px 0 auto;
        width: 349px;
        height: auto;
        color: $color-white;

        h1 {
          margin-bottom: 24px;
        }

        p {
          margin-bottom: 40px;
          opacity: 0.9;
        }
      }
    }

    .redirection:nth-child(2) {
      background-image: url(../../../public/assets/home/desktop/image-speaker-zx7.jpg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;

      section {
        margin: 0 auto 0 95px;
        width: 220px;
        height: auto;

        h4 {
          margin-bottom: 32px;
        }
      }
    }

    .redirection:nth-child(3) {
      display: flex;
      gap: 30px;

      img {
        border-radius: 8px;
        margin-right: auto;
      }

      div {
        padding: 101px 0;
        width: 540px;
        height: 320px;
        background-color: $color-grey;
        border-radius: 8px;

        section {
          margin: 0 auto 0 95px;
          width: 265px;
          height: auto;
        }

        h4 {
          margin-bottom: 32px;
        }
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .container-home {
    .div-produit {
      width: 90%;

      .redirection:nth-child(1) {
        img {
          left: 5%;
        }

        section {
          margin: 0 5% 0 auto;
        }
      }

      .redirection:nth-child(3) {
        img,
        div {
          width: 49%;
          height: 100%;
        }

        div {
          padding: 101px 0;
        }

        section {
          margin: 0 auto 0 auto;
        }
      }
    }
  }
}

@media screen and (max-width: 1020px) {
  .container-home {
    .div-produit {
      .redirection:nth-child(2),
      .redirection:nth-child(3) {
        height: 320px;
      }

      .redirection:nth-child(1) {
        height: 720px;

        img {
          left: 50%;
          transform: translateX(-50%);
          top: 60px;
          width: 197px;
          height: 237px;
          margin: 0;
        }

        section {
          text-align: center;
          margin: 300px auto 0;

          li {
            margin: auto;
          }
        }
      }

      .redirection:nth-child(2) {
        background-image: url(../../../public/assets/home/tablet/image-speaker-zx7.jpg);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
      }

      .redirection:nth-child(3) {
        gap: 11px;

        div {
          section {
            margin: auto;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 720px) {
  .container-home {
    .div-produit {
      .redirection:nth-child(3) {
        flex-direction: column;
        height: auto;

        img {
          width: 100%;
          height: 240px;
        }

        div {
          padding: 60px 0;
          width: 100%;
          height: 240px;

          section {
            margin: auto;

            li {
              margin: auto;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 620px) {
  .container-home {
    .div-produit {
      .redirection:nth-child(2) {
        background-size: auto;
        section {
          margin: 0 auto 0 24px;
        }
      }
    }
  }
}
