.form-quantity {
  display: flex;
  width: 296px;
  height: 48px;

  p {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 48px;
  }
  .control-quantity {
    width: 40px;
    height: 48px;
    cursor: pointer;
  }

  .btn {
    margin-left: auto;
  }
}

@media screen and (max-width: 930px) {
  .form-quantity {
    margin: auto;
  }
}

@media screen and (max-width: 380px) {
  .form-quantity {
    width: fit-content;
  }
}
