.container-hero {
  width: 100%;
  height: 633px;
  background-image: url(../../../public/assets/home/desktop/image-hero.jpg);
  background-position: center top -100px;
  background-size: 110% 120%;
  padding: 128px 0 0 165px;
  margin-bottom: 200px;

  section {
    width: 420px;
    height: auto;

    span,
    h1,
    p,
    li {
      color: $color-white;
    }

    span {
      display: block;
      margin-bottom: 24px;
      opacity: 0.85;
    }

    h1 {
      margin-bottom: 24px;
      text-transform: uppercase;
    }

    p {
      margin-bottom: 40px;
      width: 360px;
      opacity: 0.85;
    }

    li {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      width: 160px;
      height: 48px;
      background-color: $color-orange;

      &:hover {
        background-color: $color-orange-hover;
      }
    }
  }
}

.only-title {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: none;
  padding: 0;
  height: 239px;
  background-color: $color-black;
  margin-bottom: 160px;

  h2 {
    text-transform: uppercase;
    color: $color-white;
  }

  section {
    display: none;
  }
}

@media screen and (max-width: 1350px) {
  .container-hero {
    background-size: 125% 130%;
    background-position: center top -150px;
  }
}

@media screen and (max-width: 1250px) {
  .container-hero {
    background-image: url(../../../public/assets/home/tablet/image-header.jpg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center top -50px;
    padding: 0;
    background-color: $color-black;
    margin-bottom: 148px;

    section {
      text-align: center;
      margin: auto;

      p {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .only-title {
    margin-bottom: 120px;
  }
}

@media screen and (max-width: 667px) {
  .container-hero {
    background-image: url(../../../public/assets/home/mobile/image-header.jpg);
    background-size: auto 100%;
    height: auto;
    padding: 108px 0;

    section {
      width: 90%;
      height: auto;
    }
  }
}

@media screen and (max-width: 620px) {
  .container-hero {
    margin-bottom: 89px;
  }
}

@media screen and (max-width: 500px) {
  .only-title {
    height: 96px;
  }
}
