.container-seeProduct {
  width: 100%;
  height: auto;

  .description {
    display: flex;
    font-weight: 500;
    font-size: 15px;
    line-height: 25px;
    margin-bottom: 160px;

    h3 {
      margin-bottom: 33px;
    }

    section {
      width: 635px;
      height: auto;
      margin-right: auto;

      p {
        color: rgba($color: #000000, $alpha: 0.5);
      }
    }

    .inthebox {
      width: 350px;
      height: auto;

      ul {
        width: auto;
        height: auto;

        li:not(:last-child) {
          margin-bottom: 8px;
        }

        li {
          color: rgba($color: #000000, $alpha: 0.5);
        }
        span {
          color: $color-orange;
          margin-right: 21px;
        }
      }
    }
  }

  .div-image {
    display: flex;
    flex-flow: column wrap;
    width: 100%;
    height: 592px;
    row-gap: 32px;
    column-gap: 30px;
    margin-bottom: 160px;

    img {
      border-radius: 8px;
    }
  }

  .div-others {
    width: 100%;
    height: auto;
    text-align: center;

    h3 {
      margin-bottom: 64px;
    }

    div {
      display: flex;
      width: 100%;
      gap: 30px;
    }

    section {
      width: 350px;
      height: 471px;

      img {
        width: 100%;
        height: 318px;
        border-radius: 8px;
        margin-bottom: 40px;
      }

      h5 {
        margin-bottom: 32px;
      }

      button {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 160px;
        height: 48px;
        background-color: $color-orange;
        color: $color-white;
        cursor: pointer;

        &:hover {
          background-color: $color-orange-hover;
        }
      }
    }
  }
}

@media screen and (max-width: 1180px) {
  .container-seeProduct {
    width: 100%;

    .description {
      flex-direction: column;
      margin-bottom: 150px;

      section {
        margin: 0 0 120px;
        width: 100%;
      }

      .inthebox {
        display: flex;
        width: 100%;

        h3 {
          width: fit-content;
          height: min-content;
        }

        ul {
          margin: auto;
        }
      }
    }

    .div-image {
      height: 450px;
      flex-flow: column wrap;
      margin-bottom: 120px;

      img {
        width: 49%;
        border-radius: 8px;
      }

      img:nth-child(1),
      img:nth-child(2) {
        height: 46.4%;
      }

      img:nth-child(3) {
        height: 100%;
      }
    }
  }
}

@media screen and (max-width: 980px) {
  .container-seeProduct {
    .div-image {
      height: 400px;

      img:nth-child(1),
      img:nth-child(2) {
        height: 46%;
      }
    }

    .div-others {
      div {
        section {
          height: auto;

          img {
            height: 318px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .container-seeProduct {
    .div-others {
      div {
        gap: 11px;
        section {
          img {
            height: 280px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 750px) {
  .container-seeProduct {
    .div-image {
      height: 340px;

      img:nth-child(1),
      img:nth-child(2) {
        height: 45.2%;
      }
    }
  }
}

@media screen and (max-width: 720px) {
  .container-seeProduct {
    .div-others {
      div {
        section {
          img {
            height: 245px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .container-seeProduct {
    .div-others {
      div {
        flex-direction: column;
        row-gap: 56px;

        section {
          width: 100%;

          img {
            height: auto;
            margin-bottom: 32px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 615px) {
  .container-seeProduct {
    .description {
      margin-bottom: 88px;

      .inthebox {
        flex-direction: column;

        h3 {
          margin-bottom: 24px;
        }

        ul {
          margin: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 560px) {
  .container-seeProduct {
    .div-image {
      height: auto;
      flex-flow: wrap;

      img:nth-child(1),
      img:nth-child(2) {
        height: 180px;
      }

      img {
        width: 100%;
        height: 100px;
      }
    }
  }
}
