.container-recap {
  padding: 32px 33px;
  width: 350px;
  height: min-content;
  background-color: $color-white;
  border-radius: 8px;

  h6 {
    margin-bottom: 31px;
  }

  .div-items {
    width: 100%;
    height: auto;
    margin-bottom: 32px;

    div:not(:last-child) {
      margin-bottom: 24px;
    }

    div {
      display: flex;
      align-items: center;
      width: 100%;
      height: 64px;

      img {
        width: 64px;
        margin-right: 16px;
      }

      section {
        margin-right: auto;

        p:first-child {
          font-weight: 700;
          font-size: 15px;
          line-height: 25px;
          color: #000000;
        }
      }
    }
  }

  ul {
    width: 100%;
    height: auto;

    li:not(:last-child) span {
      color: #000;
    }

    li:last-child span {
      color: $color-orange;
    }

    li {
      display: flex;
      justify-content: space-between;
      margin-bottom: 8px;

      span {
        font-weight: 700;
        font-size: 18px;
        line-height: 25px;
      }
    }
  }

  button {
    cursor: pointer;
    background-color: $color-orange;
    color: $color-white;
    width: 100%;
    height: 48px;
  }
}

@media screen and (max-width: 1180px) {
  .container-recap {
    width: 100%;
  }
}

@media screen and (max-width: 370px) {
  .container-recap {
    padding: 20px;
  }
}
