header {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 100%;
  height: 96px;
  background-color: $color-black;

  .background {
    position: absolute;
    left: 0;
    top: 96px;
    width: 100%;
    height: 100%;
    background-color: rgba($color: #000, $alpha: 0.5);
    z-index: 9;
  }

  .icon-hamburger {
    display: none;
    width: 20px;
    height: 20px;
  }

  .logo {
    margin-right: 197px;
    width: 143px;
    height: 25px;
  }

  .div-cart {
    position: relative;
    width: auto;
    height: 23px;

    .cart {
      cursor: pointer;
      width: 25px;
      height: 23px;
    }

    span {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: -15px;
      top: -15px;
      z-index: 10;
      width: 20px;
      height: 20px;
      border-radius: 100%;
      background-color: $color-white;
    }
  }

  nav {
    display: flex;
    width: 1110px;
    height: 62px;
    border-bottom: 1px solid rgba($color: #fff, $alpha: 0.2);
  }

  ul {
    display: flex;
    gap: 34px;
    margin-right: auto;
  }

  a {
    color: $color-white;
    height: fit-content;

    &:hover {
      color: $color-orange;
    }
  }
}

.margin {
  margin-bottom: 79px;
}

@media screen and (max-width: 1180px) {
  header {
    .logo {
      margin-right: 0;
    }

    nav {
      justify-content: space-between;
      width: 90%;
    }

    ul {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 789px) {
  header {
    height: 90px;

    .background {
      top: 90px;
    }

    .icon-hamburger {
      display: block;
      margin-right: 42px;
      cursor: pointer;
    }

    .logo {
      margin-right: auto;
    }

    nav {
      justify-content: inherit;
    }

    ul {
      position: absolute;
      top: 90px;
      left: 0;
      padding: 101px 0 0;
      width: 100%;
      height: 340px;
      background-color: white;
      z-index: 10;
      transform-origin: top;
      transform: scaleY(0);
      transition: transform 0.6s;
    }
  }
}

@media screen and (max-width: 620px) {
  header {
    nav {
      ul {
        padding-top: 84px;
        height: auto;
      }
    }
  }
}
